	onLoad: function() {
		this.allRequest();

	},
	onPageScroll: function(e) {

		if (e.scrollTop > this.listTop) {
			this.isShowTop = 1;
		} else {
			this.isShowTop = false;
		}

	},

	mounted() {
		const query = uni.createSelectorQuery().in(this);
		query
			.select('.goods_list_box')
			.boundingClientRect(data => {
				// console.log(data);
				this.listTop = data.top;
			})
			.exec();
	},
	onReachBottom() {
		if (this.tabIndex == 0) {
			this.loadStatus = 'loading';
			uni.showLoading({
				title: '正在加载更多'
			});
			// 模拟分页请求 (加载更多)
			setTimeout(() => {
				const nextData = JSON.parse(JSON.stringify(this.a_list));
				this.list.push(...nextData);
				// this.$nextTick(()=>{
				//     this.$refs.wfalls.handleViewRender();
				// })
				// APP上触发不了还是setTimeout万能
				setTimeout(() => {
					this.$refs.wfalls.handleViewRender();
				}, 0);
				uni.hideLoading();
			}, 800);
		}

	},
	methods: {
		allRequest: function() {
			this.getMenu();
			this.getBanner()
		},
		//获取轮播图
		getBanner: function() {
			let _this = this
			http.ajax({
				method: 'POST',
				url: 'GetBanner',
				data: {
					shopid: 0
				},
				success: function(res) {
					let data = JSON.parse(res).data
					for (let i in data) {
						data[i].image = _this.imgUrl + data[i].thumb
					}
					_this.swiperList = data
				}
			})
		},
		//获取菜单
		getMenu: function() {
			let _this = this
			http.ajax({
				method: 'POST',
				url: 'GetCate',
				data: {
					parentid: 0
				},
				success: function(res) {
					let data = JSON.parse(res).data
					let menu_result = [];
					for (var i = 0; i < data.length; i += 8) {
						//将数组重组，每一个为一个单独的数组
						menu_result.push(data.slice(i, i + 8));
					}
					_this.menuList = menu_result;
				}
			})
		},

		swiperChange: function(e) {
			this.current = e.detail.current;
		},
		scroll: function(e) {

			this.old.scrollTop = e.detail.scrollTop;
		},
		clickTab: function(e) {
			this.tabIndex = e;
			// uni.pageScrollTo({
			// 	scrollTop: 750,
			// 	duration: 300
			// });
		},
		stickyActive: function() {
			// this.tabBgColor = '#fff'
		},
		backTop: function() {
			this.isShowTop = false;
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
		},
		showShareModalFun: function() {
			this.showShareModal = true;
		},
		goBrandList: function() {
			uni.navigateTo({
				url: '../brand/choicenessBrand'
			});
		},
		goGoodInfo: function() {
			uni.navigateTo({
				url: '../goods/goodInfo'
			});
		},
		copyWX: function(e) {
			this.copyText(e);
		},
		copyText: function(e) {
			const result = h5Copy(e);
			if (result === false) {
				this.showToast('暂不支持', 'error');
			} else {
				this.showToast('复制成功！', 'success');
			}
		},
		showToast: function(title, type, url, param) {
			this.$refs.uToast.show({
				title: title,
				type: type,
				url: url,
				param: param
			});
		},
		goFlashSale: function() {
			uni.navigateTo({
				url: '../goods/flashSale'
			});
		},
